@use 'animations';
@use 'variables';
@use 'mixins';
@use "sass:color";

@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;1,300;1,400&display=swap');



* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
  color: #181818;
}



.container {
  max-width: 1200px;
  margin: auto;
  // display: flex;
  // flex-direction: column;
}

.card {
  background-color: variables.$accent;
  border-radius: 10px;
  padding: 10px;
  margin: 10px;
  box-shadow: 0 2px 1px -1px rgba(0, 0, 0, 0.2), 0 1px 1px 0 rgba(0, 0, 0, 0.14),
    0 1px 3px 0 rgba(0, 0, 0, 0.12);
  transition: ease-in-out 200ms;

  &:hover {
    box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.2),
      0 4px 5px 0 rgba(0, 0, 0, 0.14), 0 1px 10px 0 rgba(0, 0, 0, 0.12);
  }
}



h1 {
  font-family: 'Quicksand', sans-serif;
  font-weight: 400;
  @include mixins.fluid-font(4vw, 25px, 35px, 30px);
  b {
    font-weight: 500;
    font-family: 'Quicksand', sans-serif;
  }
}

h3 {
  @include mixins.fluid-font(2vw, 15px, 20px, 20px);
  font-weight: 300;
  b {
    font-weight: 400;
  }
}

h2 {
  font-family: 'Quicksand', sans-serif;
  @include mixins.fluid-font(2vw, 15px, 20px, 20px);
  font-weight: 600;
  color: white;
}

h4 {
  color: white;
  @include mixins.fluid-font(2vw, 15px, 15px, 20px);
  font-weight: 300;
}

.is-visible {
  opacity: 1;
}

.is-hidden {
  opacity: 0;
}

.drawer {
  z-index: 10;
  position: fixed;
  background-color: white;
  height: 100vh;
  width: 100vw;
  top: 0;
  right: 0;
  box-shadow: 0 3px 3px -2px rgba(0, 0, 0, 0.2), 0 3px 4px 0 rgba(0, 0, 0, 0.14),
    0 1px 8px 0 rgba(0, 0, 0, 0.12);
  transition: ease-in-out 100ms;
  pointer-events: auto;
  h1 {
    padding: 10px;
  }

  // .image {
  //   background-image: radial-gradient(transparent, black),
  //     url(../img/Room2.37.jpg);
  //   background-repeat: no-repeat;
  //   background-size: cover;
  //   height: 50vh;
  //   max-width: 100%;

  //   transition: ease-in-out 500ms;
  // }
  // .image:hover {
  //   background-image: radial-gradient(transparent, transparent),
  //     url(../img/Room2.37.jpg);
  //   max-width: 100%;

  //   transition: ease-in-out 500ms;
  // }
}

.drawer--closed {
  opacity: 0;
  transform: scale(0.99);
  transition: ease-in-out 100ms;
  pointer-events: none;
}

button {
  z-index: 20;
  position: relative;
}

@media (max-width: 30rem) {
  .grid {
    display: flex;
    flex-wrap: wrap;

    .cell {
      flex: 1 1 100%;
      min-height: 50px;
    }
  }

  .id {
    display: flex;
    flex-direction: column;

    img {
      max-height: 250px;
      margin: 10px;
    }
    h1 {
      text-align: center;
      // font-size: 6vw;
    }
    h3 {
      text-align: center;
      // font-size: 4vw;
    }
  }
  .intro.rotate {
    .wordWrapper {
      width: 12rem;
    }
  }
}

.color1{
  @include mixins.cardColor(variables.$yellow);
}
.color2{
  @include mixins.cardColor(variables.$orange);
}
.color3{
  @include mixins.cardColor(variables.$green);
}
.color4{
  @include mixins.cardColor(variables.$purple);
}
.color5{
  @include mixins.cardColor(variables.$blue);
}
.color6{
  @include mixins.cardColor(variables.$pink);
}